:root {
  --primeblue: #1B7CEC;
  --secondaryblue: #c1deff;
  --primeblack: #333;
  --gray: #646464;
  --whiteshadow: #cce7ff;
  --white: #ffffff;
  --yellow: #ECE41B;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: inherit;

}

.center {
  margin: 0 auto;
  text-align: center;
}



.general_shadow{
  box-shadow: 1px 2px 6px 1px  #e0e0e0;
}

.white_bg{
 background-color: #ffffff;
 background-color: var(--white);
}

.flex{
  display: flex;
}

.App {
  /* font-family: Arial, Helvetica, sans-serif, sans-serif;; */
  text-align: center;
  max-width: 100%;

}

html, body{
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden; 
}

/* Start Button style general */
.btn_container {
  width: -webkit-fill-available;
  width: stretch;
}

.btn, .btn:visited, .btn:link{
  padding:7px 20px;
  font-size: 1.2rem;
  border-radius: 5px;
  cursor: pointer;
  min-width: 80px;

}

.btn:link {
  text-decoration: inherit;
}

.btn:visited {
  text-decoration: inherit;
}

.btn__primary{
  background-color: #1B7CEC;
  background-color: var(--primeblue);
  border:solid 0px #1B7CEC;
  border:solid 0px var(--primeblue);
  color : #ffffff;
  color : var(--white);
}

.btn__secondary{
  background-color: #ffffff;
  background-color: var(--white);  
  border:solid 1px #646464;  
  border:solid 1px var(--gray)
}

.btn__primary:hover, .btn__secondary:hover{
  box-shadow: 1px 2px 6px 1px  #e0e0e0;
}

.btn__primary:active, .btn__secondary:active{
  border:solid 1px #1B7CEC;
  border:solid 1px var(--primeblue);

}
/* Button style general End */


.unbold {
  font-weight:normal;
}
​
.circle{
  border-radius: 100px;
}

.fit__content{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;

}

.margin__top{
  margin-top: 10px;
}

#btn__checkout{
  width: -webkit-fill-available;
  width: fill;
}

@media (max-width:1160px) {
