.navbar {
  background-color: #fff;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 20;
}
.swit {
  flex-grow: 1;
}
.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80px;
  max-width: 1500px;
}

.nav-logo {
  align-items: center;
  margin-left: 20px;
  cursor: pointer;
  flex-grow: 1;
}

.nav_logo_img {
  text-align: center;
}
.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 2rem;
}
.nav-item:hover {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.nav-links {
  color: #000;
  font-weight: 500;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav_links_login {
  display: none;
}
.fa-code {
  margin-left: 1rem;
}

.nav-item {
  line-height: 40px;
  margin-right: 1rem;
}

.nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.5s ease;
}

.nav-item:hover:after {
  width: 100%;
}

.nav-item .active {
  color: #005cc6;
}

.nav-icon {
  display: none;
}

.iconsMenue{
  color: #064288;
}
.socialIcons {
  display: none;
}

.socialIcons_items:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.socialIcons_title p {
  font-size: 15px;
  padding: 1px;
  color: #ECE41B;
  margin-bottom: 10px;
}

.logoSidebar {
  display: none;
}
.switchertwo {
  display: none;
}

.GlobeIcon{
  color: #005cc6;
  font-size: 16px;
  text-align: center;
  align-items: center;
  
}

.GlobeIcon_text{
  font-size: 14px;
  background: none;
  border: none;
  margin: 2px;
  align-items: center;
  cursor: pointer;
  
}
@media screen and (max-width: 960px) {
  .nav_logo_img {
    width: 80px;
    text-align: left;
    display: flex;
  }
  .nav-logo {
    left: 0;
    padding-left: 15px;
    float: left;
    position: absolute;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 60%;
    height: 100vh;
    border-top: 1pxsolid #fff;
    position: absolute;
    top: 0px;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: linear-gradient(to right, #1b7cec, #005cc6);
    left: 0px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }
  .nav-item {
    display: block;
    text-align: center;
    align-items: center;
    font-size: 14px;
    margin: 0;
  }

  .nav-item .active {
    color: #d2dce8;
  }
  .nav-links {
    text-align: center;
    align-items: center;
    font-size: 15px;
    font-weight: 500;
    color: #fff;
    border-radius: 8px;
    background-color: #005cc6;
  }
  .iconsMenue{
    color: #fff;
  }
  .nav-icon {
    display: block;
    position: absolute;
    text-align: center;
    align-items: center;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #005cc6;
  }
  .switcher {
    display: flex;
    margin: auto;
    text-align: center;
    align-items: center;
  }
  .socialIcons {
    display: flex;
    margin: auto;
    color: #fff;
    cursor: pointer;
  }
  .socialIcons .socialIcons_items {
    padding: 5px;
    font-size: 20px;
  }

  .logoSidebar {
    display: table;
    margin-bottom: 10px;
    margin-top: 15px;
    padding: 10px;
    align-items: center;
    text-align: center;
  }
  .logoSidebar .nav_logo_sidebar {
    width: 80px;
  }
}

@media only screen and (max-width: 960px) {
